/**
 *
 * @param trainCode
 * @returns
 */
export function getTrainType(trainCode: string) {
  const code = parseInt(trainCode.replace(/[A-Z]/g, ""), 10);

  if (trainCode.startsWith("G")) {
    if (code >= 1 && code <= 4000) return "G: ";
    if (code >= 4001 && code <= 4998)
      return "G: ";
    if (code >= 5001 && code <= 9000)
      return "G: ";
    if (code >= 9001 && code <= 9998)
      return "G: ";
  } else if (trainCode.startsWith("C")) {
    if (code >= 1001 && code <= 9998) return "C";
  } else if (trainCode.startsWith("D")) {
    if (code >= 1 && code <= 4000) return "D: ";
    if (code >= 4001 && code <= 4998)
      return "D: ";
    if (code >= 5001 && code <= 9000) return "D: ";
    if (code >= 9001 && code <= 9998)
      return "D: ";
  } else if (trainCode.startsWith("Z")) {
    if (code >= 1 && code <= 4000) return "Z: ";
    if (code >= 4001 && code <= 4998)
      return "Z: ";
    if (code >= 5001 && code <= 9000) return "Z: ";
    if (code >= 9001 && code <= 9998)
      return "Z: ";
  } else if (trainCode.startsWith("T")) {
    if (code >= 1 && code <= 3000) return "T: ";
    if (code >= 3001 && code <= 3998) return "T: ";
    if (code >= 4001 && code <= 4998) return "T: ";
    if (code >= 5001 && code <= 9998) return "T: ";
  } else if (trainCode.startsWith("K")) {
    if (code >= 1 && code <= 4000) return "K: ";
    if (code >= 4001 && code <= 4998) return "K: ";
    if (code >= 5001 && code <= 6998) return "K: ";
    if (code >= 7001 && code <= 9998) return "K: ";
  } else if (code >= 1001 && code <= 5998) {
    if (code >= 1001 && code <= 1998)
      return ": ";
    if (code >= 2001 && code <= 2998) return ": ";
    if (code >= 4001 && code <= 5998) return ": ";
  } else if (code >= 6001 && code <= 7598) {
    if (code >= 6001 && code <= 6198) return ": ";
    if (code >= 6201 && code <= 7598) return ": ";
  }

  return "Unknown train type or code";
}

